// ブレイクポイントを指定
$pc: 1080px;
$tab: 768px;
$sp: 480px;
$page: 1150px;

@mixin pc {
    @media screen and (max-width: ($pc)) {
    @content;
}
}

@mixin tab {
    @media screen and (max-width: ($tab)) {
    @content;
    }
}

@mixin sp {
    @media screen and (max-width: ($sp)) {
    @content;
    }
}

@mixin page {
    @media screen and (max-width: ($page)) {
    @content;
}
}